import React, { useState, useEffect } from "react";
import "./../global.scss";
import "./../app.scss";
import { Link, graphql } from "gatsby";
import Flipbox from "../components/Flipbox";
import ThemeBtn from "../components/ThemeBtn";
import LangSelect from "../components/LangSelect";

export const pagesQuery = graphql`
  query BlogPostQuery {
    allMdx(filter: { slug: { glob: "!*.ar" } }) {
      edges {
        node {
          slug
          frontmatter {
            title
            date
            desc
          }
        }
      }
    }
  }
`;

const App = ({ data, ...props }) => {
  const [theme, setTheme] = useState("light");
  const [language, setLanguage] = useState("en");

  return (
    <main id={theme}>
      <div style={{ height: "100vh", flexDirection: "column" }}>
        <h1 className="huge">This is my blog of... amazing stuff</h1>
        <ThemeBtn
          style={{ backgroundColor: "#7d7b7b80" }}
          width="72px"
          theme={theme}
          setTheme={setTheme}
        />
        <LangSelect
          lang={language}
          setLang={setLanguage}
          nav={props.navigate}
        />
      </div>
      <div style={{ justifyContent: "flex-end" }}>
        <h2 className="huge">Latest</h2>
      </div>
      {data.allMdx.edges.map(({ node }) => {
        console.log(node);
        return (
          <Flipbox
            key={node.slug}
            flipContent={node.frontmatter.desc}
            frontContent={<Link to={node.slug}>{node.frontmatter.title}</Link>}
          />
        );
      })}
    </main>
  );
};

export default App;
